// @description: they are lao name of months, and days;

const { _laoMonth, _laoDayWeek } = require('./constants.js')

// @description: add prefix zeros
function addZero(your_number, length) {
  var num = '' + your_number;
  while (num.length < length) {
    num = '0' + num;
  }
  return num;
}

// @description: get the regExp for replacement
function getRegExp(str) {
  return new RegExp("\\b" + str + "\\b");
}

// @description: returning the date
function getDateMonthYearHourMinute(date, format = null) {
  let thisDate = new Date(date);
  let dayWeekFormat = _laoDayWeek[thisDate.getDay()];
  let hourFormat = addZero(thisDate.getHours(), 2);
  let minuteFormat = addZero(thisDate.getMinutes(), 2);
  let secondFormat = addZero(thisDate.getSeconds(), 2);
  let dateFormat = addZero(thisDate.getDate(), 2);
  let monthFormat = _laoMonth[thisDate.getMonth()];
  let yearFormat = thisDate.getFullYear();

  if (format === null) {
    return `${dateFormat} ${monthFormat} ${yearFormat}`
  } else {
    // D = Date, M = Month, Y = Year, H = Hour, MN = Minute, S = Second, W = Day Week
    // @description: replace all regular exp into The Correct Format
    let returnTextInCorrectFormat = format;
    // @description: replace by key
    returnTextInCorrectFormat = returnTextInCorrectFormat.replace(getRegExp('D'), dateFormat);
    returnTextInCorrectFormat = returnTextInCorrectFormat.replace(getRegExp('M'), monthFormat);
    returnTextInCorrectFormat = returnTextInCorrectFormat.replace(getRegExp('Y'), yearFormat);
    returnTextInCorrectFormat = returnTextInCorrectFormat.replace(getRegExp('H'), hourFormat);
    returnTextInCorrectFormat = returnTextInCorrectFormat.replace(getRegExp('MN'), minuteFormat);
    returnTextInCorrectFormat = returnTextInCorrectFormat.replace(getRegExp('S'), secondFormat);
    returnTextInCorrectFormat = returnTextInCorrectFormat.replace(getRegExp('W'), dayWeekFormat);
  
    // @description: replace by key
    returnTextInCorrectFormat = returnTextInCorrectFormat.replace('date', dateFormat);
    returnTextInCorrectFormat = returnTextInCorrectFormat.replace('month', monthFormat);
    returnTextInCorrectFormat = returnTextInCorrectFormat.replace('year', yearFormat);
    returnTextInCorrectFormat = returnTextInCorrectFormat.replace('hour', hourFormat);
    returnTextInCorrectFormat = returnTextInCorrectFormat.replace('minute', minuteFormat);
    returnTextInCorrectFormat = returnTextInCorrectFormat.replace('second', secondFormat);
    returnTextInCorrectFormat = returnTextInCorrectFormat.replace('week_day', dayWeekFormat);
    returnTextInCorrectFormat = returnTextInCorrectFormat.replace('day', dayWeekFormat);
    
    returnTextInCorrectFormat = returnTextInCorrectFormat.replace('date'.toUpperCase(), dateFormat);
    returnTextInCorrectFormat = returnTextInCorrectFormat.replace('month'.toUpperCase(), monthFormat);
    returnTextInCorrectFormat = returnTextInCorrectFormat.replace('year'.toUpperCase(), yearFormat);
    returnTextInCorrectFormat = returnTextInCorrectFormat.replace('hour'.toUpperCase(), hourFormat);
    returnTextInCorrectFormat = returnTextInCorrectFormat.replace('minute'.toUpperCase(), minuteFormat);
    returnTextInCorrectFormat = returnTextInCorrectFormat.replace('second'.toUpperCase(), secondFormat);
    returnTextInCorrectFormat = returnTextInCorrectFormat.replace('week_day'.toUpperCase(), dayWeekFormat);
    returnTextInCorrectFormat = returnTextInCorrectFormat.replace('day'.toUpperCase(), dayWeekFormat);

    return returnTextInCorrectFormat;
  }
}

// @description: checking valid date
Date.prototype.isValid = function () {
  return this.getTime() === this.getTime();
}

// @description: the default time
function laotime(time, format) {
  let thisTime = new Date(time);
  if (thisTime.isValid()) {
    if (typeof time === 'object') {
      return getDateMonthYearHourMinute(time, format);
    } else if (typeof time === 'number') {
      return getDateMonthYearHourMinute(time, format);
    } else if (typeof time === 'string') {
      let timeInStringFormat = parseFloat(time);
      if (typeof timeInStringFormat === 'number') {
        if (isNaN(timeInStringFormat)) {
          return 'Invalid Data';
        } else {
          timeInStringFormat = new Date(time);
          return getDateMonthYearHourMinute(timeInStringFormat, format);
        }
      } else {
        return 'Invalid Data';
      }
    }
  } else {
    let thisFormat = time;
    return getDateMonthYearHourMinute(new Date(), thisFormat);
  }
}
